import React from 'react';

const DoubleBlogPost = ({ even }) => {
  return (
    <div className={`post ${even ? 'even' : ''}`}>
      <div className="post__description"></div>
      <div className="post__image"></div>
    </div>
  );
}

const BlogPost = ({ index, post }) => {

  index++;

  if(index % 3 === 0) {
    return (
      <div className="post">
        <div className="post__description"></div>
        <div className="post__image"></div>
        <div className="post__video"></div>
      </div>
    );
  }

  return <DoubleBlogPost even={index % 2 === 0} />;
};

export default BlogPost;
